module.exports = "<p>The truth is that it is incredibly frustrating watching people using a platform that has so much potential and yet seems so complex. At the same time we do see a need for a streamlined experience that does a different take on how the platform can and should be used. In short - what you find here attempts to declutter the experience and just allows getting the job done.</p>\n<p>And the latter is important - getting the job done. We are not quite there yet, it is early days. But we will get there. And we will have a singular focus on doing exactly that.</p>\n<p>What we put out is and will be very different from what is seen as the established ways of doing the job.</p>\n";

